/*Slick slider modifications*/
.slick-slider {
  height: 100%;
}

.slick-track, .slick-list {
  height: 100%;
}

.slick-slide, .slick-slide > div {
  height: 100% !important;
}

.slick-slide > div > div {
  background-size: cover !important;
  background-repeat: no-repeat !important;
}

.slick-arrow.slick-next{
  right: 25px;
}

.slick-arrow.slick-prev{
  display: none;
}

.slick-dots{
  bottom: 25px !important;
}

.banner .slick-slide > div > div{
  display: flex !important;
}

.card:not(.large) .slick-slide {
  padding-right: 1rem !important;
}

/*Move out of the hidden space for slider since we are using 3.2*/
.card.large .slick-slide[data-index='0'] .card-details {
  padding-left: 3.5rem;
}
.hover\:bg-gray-500:hover {
  --tw-bg-opacity: 1;
  background-color: rgb(107 114 128 / var(--tw-bg-opacity));
}
.hover\:bg-cover:hover {
  background-size: cover;
}
.hover\:text-slate-400:hover {
  --tw-text-opacity: 1;
  color: rgb(148 163 184 / var(--tw-text-opacity));
}
.hover\:opacity-0:hover {
  opacity: 0;
}
.hover\:opacity-100:hover {
  opacity: 1;
}
.group:hover .group-hover\:opacity-30 {
  opacity: 0.3;
}
@media (min-width: 768px) {

  .md\:mr-2 {
    margin-right: 0.5rem;
  }

  .md\:px-4 {
    padding-left: 1rem;
    padding-right: 1rem;
